                childrenTable,
                this.dictMap,
                this.columnTypeMap
              );
              childrenData.forEach(function (item) {
                item.isEdit = false;
              });
              this.$forceUpdate();
              resolve(childrenData);
            }
          });
        } else {
          console.log(row.children);
