    //手动选专家确定
    submitSelectExpert(){
        let _this = this
        let saveArr = []
        let groupLeader = {}
        // let isBatchPackage = false
        // if(_this.tempIsBatch == 1){
        //   isBatchPackage = true
        // }else{
        //   isBatchPackage = false
        // }
        groupLeader.expertId = _this.selectProfessorArr.groupLeader
        groupLeader.expertName = _this.groupLeaderName
        groupLeader.isLeader = 1
        groupLeader.feaAuditid = _this.tempFeaAuditid
        groupLeader.itemId = _this.tempItemId
        groupLeader.isBatchPackage = _this.tempIsBatch
        saveArr.push(groupLeader) //添加组长
        
        _this.selectProfessorArr.reviewUsers.forEach(function(item){  //添加评审专家
            let tempReviewUser = {}
            tempReviewUser.expertId = item.expertId
            tempReviewUser.expertName = item.expertName
            tempReviewUser.isLeader = 2
            tempReviewUser.feaAuditid = _this.tempFeaAuditid
            tempReviewUser.itemId = _this.tempItemId
            tempReviewUser.isBatchPackage = _this.tempIsBatch
            saveArr.push(tempReviewUser)
        })

        if(typeof(_this.selectProfessorArr.expectUsers)!= "undefined"){
            _this.selectProfessorArr.expectUsers.forEach(function(item){  //添加技经专家
              let tempExpectUser = {}
              tempExpectUser.expertId = item.expertId
              tempExpectUser.expertName = item.expertName
              tempExpectUser.isLeader = 3
              tempExpectUser.feaAuditid = _this.tempFeaAuditid
              tempExpectUser.itemId = _this.tempItemId
              tempExpectUser.isBatchPackage = _this.tempIsBatch
              saveArr.push(tempExpectUser)
          })
        }
        // 调用流程中心
        _this.sureBtn1 = true
		    // 调用流程中心
		    if(this.$store.state.user.workflowtype==0) {
          let specRoleID = '';//专项角色ID
          let iscOrg = window.JSON.parse(sessionStorage.getItem("iscOrg"));//isc组织机构
          let specRoleMap = window.JSON.parse(sessionStorage.getItem("specRoleMap"));//isc专项role
          let purchaseRoleMap = window.JSON.parse(sessionStorage.getItem("purchaseRoleMap"));//isc零购专项role
          let electronicRoleMap = window.JSON.parse(sessionStorage.getItem("electronicRoleMap"));//isc电网基建专项role
          let userid = sessionStorage.getItem("userId");
          let params = {
              "user": userid,
              "processDefName": "expert_selection",
              "processInstName": '',
              "processInstDesc": ''
          }
          params.itemId = _this.tempItemId
          params.processInstName = _this.tempItemName+'-专家确认'
          params.processInstDesc = _this.tempItemName+'-专家确认'
          //调用流程中心启动
          
              for(let i in saveArr){
                let item = saveArr[i];
                if(item.isLeader == 1){
                  params.processInstName = _this.tempItemName+'-组长确认'
                  params.processInstDesc = _this.tempItemName+'-组长确认'
                }else{
                  params.processInstName = _this.tempItemName+'-专家确认'
                  params.processInstDesc = _this.tempItemName+'-专家确认'
                }
                createWithOptionAndReturnWorkItem(params).then(res => {
                  if(res.data.status == 0){
                    let workItemID = res.data.data.workItem.workItemID
                    let processInstID = res.data.data.processInstID
                    let param = {
                        "user":userid,
                        "workItemID":workItemID,//工作项id 上一步返回值中查找
                        "activityDefID":"ZJQR",//活动定义id
                        "participants":[]
                    }
                    param.participants.push(item.expertName)
                    // 指定专家给专家发待办
                    appointActivityParticipantWithIscLoginName(param).then(res => {
                      if(res.data.status == 0){
                          let params = {
                            "user": userid,
                            "workItemID": param.workItemID
                          }
                          finishWorkItem(params).then(res => {
                            if(res.data.status == 0){
                              let finishparams = {
                                  processInstID: processInstID,
                                  userId: userid,
                                  itemId:item.itemId,
                                  workItemID: param.workItemID,
                                  isLeader:item.isLeader,
                                  expertId:item.expertId,
                                  itemPhaseDet:'10',
                                  isBatchPackage:_this.tempIsBatch,
                                  totalNum:saveArr.length                              
                                }
                              // 将返回值信息及项目信息插入后端数据库
                              createProcess(finishparams).then(finishres => {
                                  if (finishres.data.successful) {
                                      let saveWorkItemParams ={
                                          itemId:item.itemId,
                                          itemPhaseDet:'10',
                                          isBatchPackage:_this.tempIsBatch
                                      }
                                      if(i == saveArr.length-1){
                                          
                                          updateItemPhaseDet(saveWorkItemParams).then(res=>{
                                            if (res.data.successful) {
                                              // //选择专家
                                              selectExpertsByIds(saveArr).then(response=>{
                                                _this.sureBtn1 = false
                                                if(response.data.successful){
                                                    _this.$message({
                                                        showClose: true,
                                                        message: '选择专家成功',
                                                        type: 'success'
                                                      });
                                                      _this.getTable(_this.currentPage,_this.currentNum)
                                                      // _this.dialogFormVisible = false
                                                      _this.selectProfessorArrShow = {
                                                          "groupLeader":[], //组长
                                                          "reviewUsers":[], //评审专家
                                                          "expectUsers":[]  //技经专家
                                                      }
                                                      _this.$refs.selectExpert2.clearSelection();
                                                      _this.$refs.selectExpert3.clearSelection();
                                                      _this.selectProfessorArr = {
                                                          "groupLeader":"",//组长
                                                          "reviewUsers":[],//评审专家
                                                          "expectUsers":[] //技精专家
                                                      }
                                                      _this.showSelectExpertResult2 = false
                                                      _this.showExpectDialog = false
                                                }else{
                                                      _this.$message({
                                                        showClose: true,
                                                        message: '选择专家失败',
                                                        type: 'error'
                                                      });
                                                }
                                              })
                                            }else {
                                                // let error = res.data.resultHint;
                                                _this.sureBtn1 = false
                                                this.$message.error("选择专家失败");
                                            }
                                          })
                                      }
                                      
                                  }else {
                                      // let error = finishres.data.resultHint;
                                      _this.sureBtn1 = false
                                      this.$message.error("选择专家失败");
                                  }
                              });
                            }else{
                              _this.sureBtn1 = false
                            }
                          })
                        
                      }    
                    }) 
                  }else{
                    _this.sureBtn1 = false
                  }
                })                              
              }	  
        }        
    },
    //手动选专家点击取消
    cancleSelectExpert(){
       let _this = this
       _this.$refs.selectExpert2.clearSelection();
