    //自动选择专家校验专家数不能为负数
    replaceNum(val,index){
        // val=val.replace(/\D|^0/g,'')
        let _this = this
        val = val+'';
        if(val<0){
           _this.autoSelectExpertParam.expertMajor[index].num = 1
        }else if(val.indexOf('.')>0){
           _this.autoSelectExpertParam.expertMajor[index].num = 1
        }
    },
    replaceNum1(val){
        // val=val.replace(/\D|^0/g,'')
        let _this = this
        val = val+'';
        if(val<0){
           _this.autoSelectExpertParam.goodExpertNum = 1
        }else if(val.indexOf('.')>0){
           _this.autoSelectExpertParam.goodExpertNum = 1
        }
    },
    //查询专家专业下拉列表
    getExpertMajorDict(){
        let _this = this
        getDictMap({"codeValue":"professional"}).then(response=>{
             if(response.data.successful){
               let arr = response.data.resultValue;
               for(let i=0;i<arr.length;i++){
                  let item = arr[i]
                    if(item.codeValue=="professional"||
                          item.codeValue=="0007"||
                          item.codeValue=="0008"||item.codeValue=="0006"){
                         continue
                    }
                     _this.expertMajorDict.push(item)
                     _this.professionalList.push(item)
               }
             }else{
                this.$message.error("查询专家专业失败")
             }
        })
    },
    getProfessionalStr(val){
      let _this = this
      for(let i=0;i<_this.expertMajorDict.length;i++){
          let item = _this.expertMajorDict[i]
          if(item.codeValue==val){
              return item.codeName
          }
      }
    },
    //自动选择专家确认
    submitAutoSelectExpert(){
        let _this = this
        // 调用流程中心
		    if(this.$store.state.user.workflowtype==0) {
          let specRoleID = '';//专项角色ID
          let iscOrg = window.JSON.parse(sessionStorage.getItem("iscOrg"));//isc组织机构
          let specRoleMap = window.JSON.parse(sessionStorage.getItem("specRoleMap"));//isc专项role
          let purchaseRoleMap = window.JSON.parse(sessionStorage.getItem("purchaseRoleMap"));//isc零购专项role
          let electronicRoleMap = window.JSON.parse(sessionStorage.getItem("electronicRoleMap"));//isc电网基建专项role
          let userid = sessionStorage.getItem("userId");
          let params = {
              "user": userid,
              "processDefName": "expert_selection",
              "processInstName": '',
              "processInstDesc": ''
          }
          params.itemId = _this.tempItemId
          params.processInstName = _this.tempItemName+'-专家确认'
          params.processInstDesc = _this.tempItemName+'-专家确认'
          //调用流程中心启动
          // let isBatchPackage = false
          // if(_this.tempIsBatch == 1){
          //   isBatchPackage = true
          // }else{
          //   isBatchPackage = false
          // }
          _this.sureBtn2 = true 
            for(let i in _this.totalArr){
              let item = _this.totalArr[i];
              if(item.isLeader == 1){
                params.processInstName = _this.tempItemName+'-组长确认'
                params.processInstDesc = _this.tempItemName+'-组长确认'
              }else{
                params.processInstName = _this.tempItemName+'-专家确认'
                params.processInstDesc = _this.tempItemName+'-专家确认'
              }
              createWithOptionAndReturnWorkItem(params).then(res => {
                if(res.data.status == 0){
                  let workItemID = res.data.data.workItem.workItemID
                  let processInstID = res.data.data.processInstID
                  let param = {
                      "user":userid,
                      "workItemID":workItemID,//工作项id 上一步返回值中查找
                      "activityDefID":"ZJQR",//活动定义id
                      "participants":[]
                  }
                  param.participants.push(item.userName)
                  // 指定专家给专家发待办
                  appointActivityParticipantWithIscLoginName(param).then(res => {
                    if(res.data.status == 0){
                        let params = {
                          "user": userid,
                          "workItemID": workItemID
                        }
                        finishWorkItem(params).then(res => {
                          if(res.data.status == 0){
                              let finishparams = {
                                processInstID: processInstID,
                                userId: userid,
                                itemId:item.itemId,
                                workItemID: workItemID,
                                isLeader:item.isLeader,
                                expertId:item.expertId,
                                itemPhaseDet:'10',
                                isBatchPackage:_this.tempIsBatch,
                                totalNum:_this.totalArr.length
                              }
                            // 将返回值信息及项目信息插入后端数据库
                            createProcess(finishparams).then(finishres => {
                                if (finishres.data.successful) {
                                    let saveWorkItemParams ={
                                        itemId:item.itemId,
                                        itemPhaseDet:'10',
                                        isBatchPackage:_this.tempIsBatch
                                    }
                                    if(i == _this.totalArr.length-1){
                                    
                                      updateItemPhaseDet(saveWorkItemParams).then(res=>{
                                          if (res.data.successful) {
                                              saveAutoSelectExperts(_this.totalArr).then(response=>{
                                                _this.sureBtn2 = false
                                                if(response.data.successful){
                                                    _this.$message({
                                                        showClose: true,
                                                        message: '选择专家成功',
                                                        type: 'success'
                                                      });
                                                      _this.showSelectExpertResult = false
                                                      _this.getTable(_this.currentPage,_this.currentNum)
                                                    
                                                }else{
                                                    _this.$message({
                                                      showClose: true,
                                                      message: '选择专家失败',
                                                      type: 'error'
                                                    });
                                                }
                                              })
                                          }else {
                                            _this.sureBtn2 = false
                                              this.$message.error("选择专家失败");
                                          }
                                      })
                                    }
                                }else {
                                  _this.sureBtn2 = false
                                    this.$message.error("选择专家失败");
                                }
                            });
                          }else{
                            _this.sureBtn2 = false
                          }
                        })
                      
                    }else{
                      _this.sureBtn2 = false
                    }    
                  })
                }else{
                  _this.sureBtn2 = false
                }  
              })    
            }	  
        }
        
    },
    //自动选专家
    autoSelectExpert(){
      let _this = this
      console.log(_this.autoSelectExpertParam)
      let param = []
      
      //组长
      let leaderMajor = {}
      leaderMajor.type = 1
      leaderMajor.professional = _this.autoSelectExpertParam.leaderMajor

      //校验
      if( _this.autoSelectExpertParam.leaderMajor==""){
          _this.$message({
                showClose: true,
                message: '请选择组长专业',
                type: 'warning'
              });
           return false
      }
      leaderMajor.professionalStr = _this.getProfessionalStr(_this.autoSelectExpertParam.leaderMajor)
      leaderMajor.itemId = _this.tempItemId
      leaderMajor.feaAuditId = _this.tempFeaAuditid
      param.push(leaderMajor)

      //技经专家
      let goodExpert = {}
      goodExpert.type = 3
      goodExpert.count = _this.autoSelectExpertParam.goodExpertNum
      goodExpert.itemId = _this.tempItemId
      goodExpert.feaAuditId = _this.tempFeaAuditid

      param.push(goodExpert)

      for(let i=0;i<_this.autoSelectExpertParam.expertMajor.length;i++){
          let temp = _this.autoSelectExpertParam.expertMajor[i]
          let tempParam = {}
          tempParam.type = 2
          tempParam.professional = temp.major
          if(tempParam.professional==''){
            _this.$message({
                    showClose: true,
                    message: '请选择专家专业',
                    type: 'warning'
                  });
              return false
          }
          tempParam.professionalStr = _this.getProfessionalStr(temp.major)
          tempParam.count = temp.num
          if(tempParam.count<1){
            _this.$message({
                    showClose: true,
                    message: '专家人数要大于0',
                    type: 'warning'
                  });
              return false
          }
          tempParam.itemId = _this.tempItemId
          tempParam.feaAuditId = _this.tempFeaAuditid
          param.push(tempParam)
      }
      _this.autoLoading = true
      autoSelectExperts(param).then(response=>{
        _this.autoLoading = false
          if(response.data.successful){
              _this.showExpectDialog = false

              //选择完专家之后查询已选择的专家
              _this.showSelectExpertResult = true
              let labelArr = response.data.resultValue.tableLable.items
 
              _this.selectedExpertLable = _this.getLabelList(labelArr)
              _this.selectedExpertDictMap = _this.getDictArr(labelArr)
              _this.selectedExpertColumnMap = _this.getColumnTypeArr(labelArr)

              let tableData = response.data.resultValue.tableData
              _this.totalArr = []
              for(let i=0;i<tableData.length;i++){
                  let arrT = tableData[i]
                  for(let j=0;j<arrT.expertsList.length;j++){
                      arrT.expertsList[j].isBatchPackage = _this.tempIsBatch
                      _this.totalArr = _this.totalArr.concat(arrT.expertsList[j])
                  }  
              }
              let leaderArr = []
              let groupExpertArr = []
              let goodExpertArr = []
              for(let i=0;i< _this.totalArr.length;i++){
                    let tempObj =  _this.totalArr[i]
                    if(tempObj.isLeader==1){//组长
                        leaderArr.push(tempObj)
                    }else if(tempObj.isLeader==3){
                         goodExpertArr.push(tempObj)
                    }else if(tempObj.isLeader==2){
                       
                        groupExpertArr.push(tempObj)
                    }
                }

             _this.selectedExpertLeader = _this.getShowDateTable(leaderArr,_this.selectedExpertDictMap,_this.selectedExpertColumnMap)
             _this.selectedExpert = _this.getShowDateTable(groupExpertArr,_this.selectedExpertDictMap,_this.selectedExpertColumnMap)
             _this.selectedGootExpert = _this.getShowDateTable(goodExpertArr,_this.selectedExpertDictMap,_this.selectedExpertColumnMap)


          }else{
               let str = response.data.resultHint
               _this.$message({
                    showClose: true,
                    message: '选择专家失败 '+str,
                    dangerouslyUseHTMLString: true,
                    type: 'error'
                });
          }
      })
    },

    //动态删除输入框
    removeDomain(domain){
    
        var index = this.autoSelectExpertParam.expertMajor.indexOf(domain)
        if (index !== -1) {
          this.autoSelectExpertParam.expertMajor.splice(index, 1)
        }
    },
